import React, { Component } from 'react';
export default class Debug extends Component {
    render() {
        const o = this.props.o;
        return (
            <div className='debug' style={{position:'absolute'}}>
                <button onClick={o.testUsersReady}></button>
                <button onClick={o.deal}></button>
                <button onClick={o.test1}></button>
                <button onClick={o.testActive}></button>
                <button onClick={o.test3}></button>
                <br />
                <button onClick={o.testDummy.bind(o, 'east')}></button>
                <button onClick={o.testDummy.bind(o, 'west')}></button>
                <button onClick={o.testDummy.bind(o, 'north')}></button>
                <br />
                <button onClick={o.testBid}></button>
                <button onClick={o.testBid1}></button>
                <button onClick={o.testClock}></button>
                <button onClick={o.testLastTrick}></button>
                <br />
                <button onClick={o.showResult}></button>
            </div>
        )
    }
}


